/* ToDo.css */

body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #f4f4f9;
}

.todo {
    text-align: center;
    padding: 20px;
    margin: 20px auto;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 600px;
}

.todo h1 {
    font-size: 2rem;
    margin-bottom: 10px;
    color: #333;
}

.textInputContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
}

.textInput input {
    width: 75%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 1rem;
    outline: none;
    transition: 0.3s;
}

.textInput input:focus {
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.btn button {
    padding: 10px 20px;
    margin-left: 10px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    transition: 0.3s;
}

.btn button:hover {
    background-color: #0056b3;
}

.list {
    margin-top: 20px;
    text-align: left;
    padding: 10px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.list h3 {
    margin-bottom: 10px;
    font-size: 1.5rem;
    color: #333;
}

.list p {
    color: #777;
    font-style: italic;
}

fieldset {
    border: none;
}

.list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ddd;
    font-size: 1rem;
}

.list-item:last-child {
    border-bottom: none;
}

.list-item span {
    flex-grow: 1;
    text-align: left;
}

.list-item span.completed {
    text-decoration: line-through;
    color: #777;
}

.list-item button {
    margin-left: 10px;
    padding: 5px 10px;
    font-size: 0.9rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.list-item .toggle {
    background-color: #28a745;
    color: #fff;
}

.list-item .toggle:hover {
    background-color: #218838;
}

.list-item .edit {
    background-color: #ffc107;
    color: #212529;
}

.list-item .edit:hover {
    background-color: #e0a800;
}

.list-item .delete {
    background-color: #dc3545;
    color: #fff;
}

.list-item .delete:hover {
    background-color: #c82333;
}

@media (max-width: 768px) {
    .textInput input {
        width: 65%;
    }

    .btn button {
        font-size: 0.9rem;
    }

    .list h3 {
        font-size: 1.3rem;
    }
}
